.item{
    padding: 5px;
    border: 1px black solid;
    width: 3rem;
    height: 3rem;
    text-align: center;
    outline: none;
    caret-color: transparent;
    cursor: pointer;
    font-size: 2rem;
}

.item-permanent{
    font-weight: 900;
}

.item-mistake{
    background-color: #ff8080;
}

.item:focus{
    background-color: darkgray;
}

.item-mistake:focus{
    background-color: #d27979;
}
